import React from 'react';
import { Refer } from '@platform/base'

export default function createRefer(refcode, config = {}) {
    switch (refcode) {
        case 'cont':
            return <Refer refName={''} refCode={'cont'} queryGridUrl={'/nccloud/reva/ref/cont.do'} {...config} />;

        case 'dept':
            return (
                <Refer
                    refName={''}
                    refCode={'dept'}
                    refType={'tree'}
                    queryTreeUrl={'/nccloud/reva/ref/dept.do'}
                    {...config}
                />
            );

        case 'finance':
            return (
                <Refer
                    refName={''}
                    refCode={'finance'}
                    refType={'tree'}
                    queryTreeUrl={'/nccloud/reva/ref/finance.do'}
                    {...config}
                />
            );

        case 'materialclass':
            return (
                <Refer
                    refName={''}
                    refCode={'materialclass'}
                    // refType={'tree'}
                    queryGridUrl={'/nccloud/reva/ref/materialclass.do'}
                    {...config}
                />
            );

        case 'material':
            return (
                <Refer
                    refName={''}
                    refCode={'material'}
                    refType={'gridTree'}
                    isMultiSelectedEnabled={true}
                    queryGridUrl={'/nccloud/reva/ref/material.do'}
                    queryTreeUrl={'/nccloud/reva/ref/materialclass.do'}
                    {...config}
                />
            );

        case 'obligation':
            return (
                <Refer
                    refName={''}
                    refCode={'obligation'}
                    refType={'tree'}
                    queryTreeUrl={'/nccloud/reva/ref/obligation.do'}
                    {...config}
                />
            );

        default:
            return (
                <Refer
                    refName={'test'}
                    refCode={'test'}
                    // refType={'tree'}
                    queryGridUrl={'/test/test.do'}
                    {...config}
                />
            );

    }
}

