import React, { Component, Fragment, cloneElement, isValidElement } from "react";
import classNames from "classnames";
import { Item, itemCreator } from "@platform/template";
import CellIcon from "./CellIcon";
import {
    isString,
    isFunction,
    isWrong,
    isArray,
    isBoolean,
    ncRounding,
    formatAcuracy,
    isNullOrVoid,
    isObject,
    handleBrowseData,
    isIE,
} from "../../utils/index";
import { getSysFieldid, isArrowKeyDown, getLangCode } from "@platform/api";
import { Tooltip } from '@platform/base';
import CONFIG from '../../config';
import pureRender from './pureRender';
require("./cell.less");

const TIME_TYPES = { datepicker: true, NCTZDatePickerStart: true, NCTZDatePickerEnd: true, timepicker: true, datetimepicker: true, rangepicker: true, datePickerNoTimeZone: true };

// get nearest parent element matching selector   origin from stackoverflow  add by bbqin
const Closest = function (el, selector) {
    if (!el) {
        return null;
    }
    var matchesSelector = el.matches || el.webkitMatchesSelector || el.mozMatchesSelector || el.msMatchesSelector;

    while (el) {
        if (matchesSelector.call(el, selector)) {
            break;
        }
        el = el.parentElement;
    }
    return el;
};

/**
 * 
 * @param {*} langJson 
 * @param {*} itemtype 
 * @param {*} value 
 * @param {*} scale 
 * @param {*} attrcode 
 * @param {*} languageMeta 
 * @param {*} values 
 * @param {*} datatype 
 * @param {*} options 
 * @param {*} multiple 
 */
const handleCellValue = function (
    langJson = {},
    itemtype,
    value = {},
    scale,
    attrcode,
    languageMeta,
    values,
    datatype,
    options,
    multiple,
) {
    switch (true) {
        case itemtype === "number":
            // 
            // value.value = formatAcuracy(value, scale);
            break;
        case CONFIG.boolean.includes(itemtype):
            value = JSON.parse(JSON.stringify(value || {}));
            value.display = value.value == 'Y' || value.value == true ? langJson && langJson['table_yes'] : langJson && langJson['table_no'];
            value.value = value.value == 'Y' || value.value == true ? true : false;
            break;
        default:
            // isNullOrVoid(value.display) ? value.value : value.display || value.value;
            break;
    }
    return value;
};

// record
// @pureRender()
class Cell extends Component {
    constructor(props) {
        super(props);
        // state
        this.state = { tipStatus: false, autoOpen: false, showIcon: true, showTip: false };
        // 
        this.defaultItemCreator =
            itemCreator({ itemtype: props.itemtype }) || {};
        //   props 
        this.cellStatus = this.getCellStatus(props);
        // rowKeyValue
        let { record, attrcode, scale, rowKey, store, render, itemtype, loginLanguage } = props;
        this.rowKeyValue = isObject(record[rowKey]) ? record[rowKey].value : record[rowKey];
        if (render && itemtype === "customer") {
            this.unregister = store?.registerField([this.rowKeyValue, 'sp_col_customer__', attrcode], this);
        }
        // 
        this.cellScale = this.getScale(record, attrcode, scale);
        // 
        this.cellValue = this.getValue(props, attrcode, this.cellScale, this.cellStatus.editable, loginLanguage);
    }

    UNSAFE_componentWillReceiveProps(nextProps) {
        //   props 
        this.cellStatus = this.getCellStatus(nextProps);
        // rowKeyValue
        let { record, attrcode, scale, rowKey, store, render, itemtype, loginLanguage } = nextProps;

        let rowKeyValue = isObject(record[rowKey]) ? record[rowKey].value : record[rowKey];
        if (this.rowKeyValue !== rowKeyValue) {
            if (render && itemtype === "customer") {
                this.unregister();
                // setCellValue  
                this.unregister = store?.registerField([rowKeyValue, 'sp_col_customer__', attrcode], this);
            }
            this.rowKeyValue = rowKeyValue;
        }

        // 
        this.cellScale = this.getScale(record, attrcode, scale);
        // 
        this.cellValue = this.getValue(nextProps, attrcode, this.cellScale, this.cellStatus.editable, loginLanguage);
    }

    componentDidMount() {
        // 
        if (this.cellStatus.isEdit) {
            let {
                attrcode,
                store,
            } = this.props;
            let $focusElement = this.cellDom && this.cellDom.querySelector('[tabindex="0"]');
            if ($focusElement && !Closest($focusElement, '.u-table-row-fixed-columns-in-body')) {
                //  
                $focusElement.ncExecuteFocus = true;
                $focusElement.focus();
                $focusElement.ncExecuteFocus = false;
                store && store.setCellProps(this.rowKeyValue, attrcode, { isEdit: undefined }, false);
            }
        }
    }

    componentDidUpdate() {
        // 
    }

    componentWillUnmount() {
        this.cellDom = null;
        this.defaultItemCreator = null;
        this.rowKeyValue = null;
        this.cellValue = null;
        this.unregister?.();
    }

    /**
     * @description: 
     * @param {type}
     * @return {type}
     */
    getScale(record, attrcode, scale) {
        let values = record.values || record || {};
        let value = values[attrcode];
        let valueScale = isObject(value) ? value.scale : scale;
        valueScale = !isWrong(valueScale) && valueScale != "-1" ? +valueScale : +scale;
        // console.log(valueScale, attrcode);
        return valueScale || 0;
    }

    /**
     * @description: 
     * @param {type}
     * @return:
     */
    getValue = (props, attrcode, scale, editable, loginLanguage) => {
        let { getCellValue, record, rowKey, rowIndex, itemtype, languageMeta, langJson = {} } = props;
        // console.log(langJson);
        let value = {};
        if (getCellValue) {

            //  item
            if (loginLanguage && itemtype === "residtxt") {
                let loginValue,
                    mainValue;
                Array.isArray(languageMeta) && languageMeta.map(({ index, languageCode }) => {
                    if (languageCode === loginLanguage) {
                        // 
                        let realCode = index == 1 ? attrcode : attrcode + index;
                        loginValue = getCellValue({
                            attrcode: realCode,
                            rowKey,
                            rowKeyValue: record[rowKey],
                            rowIndex,
                        });
                    }
                    if (index == 1 || index == '') {
                        // 
                        mainValue = getCellValue({
                            attrcode,
                            rowKey,
                            rowKeyValue: record[rowKey],
                            rowIndex,
                        });
                    }
                });
                if (loginValue && loginValue.value) {
                    value = loginValue;
                }
                if (mainValue && mainValue.value) {
                    value = mainValue;
                }
            } else {
                value = getCellValue({
                    rowKey,
                    rowKeyValue: record[rowKey],
                    rowIndex,
                    attrcode,
                });
            }
        } else {
            !record.values && console.error("record");
            value = record.values[attrcode];
            if (
                !value ||
                value.display === undefined ||
                value.value === undefined
            ) {
                // console.error('values');
                !value && (value = {});
            }
            value.display = value.display || value.value || "";
            value.value = value.value || "";
        }

        //  // item
        value = !editable ? handleCellValue(langJson, itemtype, value, scale) : value;
        return value;
    };

    /**
     *     
     * @description:  
     * @param {type}
     * @return:
     */
    getCellStatus = props => {
        let {
            // 
            status = "browse",
            editType = "toggle",

            disabled = false,
            visible = true,
            // 
            selected = false,
            focused = false,
            actived = false,
            // 
            itemtype,
            // 
            render,
            //  // static hover none
            cellIcon,
            // 
            disableEditType = false,

            renderStatus = "edit",
            renderItem,
            // page
            renderItems = {},
            attrcode,
            record,
        } = props;
        // 
        let {
            disabled: rowDiabled = false,
            visible: rowVisible = true,
            isEdit: rowIsEdit = false,
            selected: rowSelected = false,
            focused: rowFocused = false,
        } = record || {};
        // values
        let values = record.values || record || {};
        // 
        let {
            //   
            disabled: cellDisabled,
            visible: cellVisible = true,
            isEdit: cellIsEdit = false,
            selected: cellSelected = false,
            focused: cellFocused = false,
            actived: cellActived = false,
            showDrag = false,
            validated = true,
            leftLine = false,
            rightLine = false,
            bottomLine = false,
            topLine = false,
            focusToEdit = false,
            autoFocus,
        } = values[attrcode] || {};

        // 
        let isSagaStatus = values && values.saga_status && values.saga_status.value === '1';

        //  TODO
        let oldCellDisabled = isString(cellDisabled)
            ? cellDisabled === "off"
            : cellDisabled;

        //     (!!render || )
        disabled = isBoolean(cellDisabled)
            ? oldCellDisabled
            : disabled || oldCellDisabled;

        //    
        visible = visible && rowVisible && cellVisible;

        //   
        actived = actived || cellActived;

        // 
        let tableEdit = status === "edit";

        // 
        editType = editType === "toggle" ? "toggle" : "normal";

        // 
        selected = selected || rowSelected || cellSelected;

        // 
        focused = rowFocused || cellFocused;

        // switch 
        let isSwitchType =
            itemtype === "switch_browse" ||
            itemtype === "checkbox_switch" ||
            itemtype === "switch";
        // switch 
        let isCheckboxSwitchType = itemtype === "checkbox_switch";

        // 
        let editable = itemtype === "switch_browse" ? true : tableEdit && !disabled;

        // 
        let isEdit = rowIsEdit || cellIsEdit;

        editType = itemtype === "switch_browse" ||
            (tableEdit && itemtype === "checkbox_switch") ||
            (tableEdit && itemtype === "switch")
            ? "normal" : "toggle";

        // cellIcon
        cellIcon = (render && itemtype === "customer") || isSwitchType ? "none" : cellIcon || "hover";
        // 
        let isCellIconInRight = itemtype === "select" || itemtype === "refer" || TIME_TYPES[itemtype];

        // switch_browse    
        if (itemtype === "switch_browse") {
            if (tableEdit) {
                disabled = true;
            }
        }

        // label 
        if (itemtype === "label") {
            // disabled = true;
            editable = false;
        }

        // attachment 
        let isAttachment = false,
            disableModify = false;
        if (itemtype === "attachment") {
            editable && (disableModify = true);
            isAttachment = true;
            editable = true;
            editType = 'normal';
            // 
            cellIcon = 'none';
        } else if (disableEditType) {
            // 
            editable = false;
            editType = 'toggle';
        }

        // 
        let renderType = 'normal';
        renderItem = renderItems[attrcode];
        if (renderItem) {
            renderType = 'renderInItem';
        } else if (render && itemtype === "customer") {
            renderType = 'renderInCustomer';
        } else if (render && renderStatus == 'edit') {
            renderType = 'renderInEdit';
        } else if (render && renderStatus == 'browse') {
            renderType = 'renderInBrowse';
        }

        let renderItemInEdit = false;
        if (disableEditType) {
            renderItemInEdit = false;
        } else if ((tableEdit && itemtype != 'label' && itemtype != 'customer') || (itemtype === "switch_browse")) {
            renderItemInEdit = true;
        }

        return {
            disabled,
            tableEdit,
            isEdit,
            editable,
            editType,
            visible,
            selected,
            focused,
            actived,
            showDrag,
            cellIcon,
            isAttachment,
            disableModify,
            validated,
            leftLine,
            rightLine,
            bottomLine,
            topLine,
            focusToEdit,
            renderType,
            isSwitchType,
            isCheckboxSwitchType,
            autoFocus,
            isSagaStatus,
            isCellIconInRight,
            renderItemInEdit,
        };
    };

    /**
     * @description:
     * @param {type}
     * @return:
     */
    getCellClassName() {
        let {
            record,
            rowIndex,
            attrcode,
            cellClassName,
        } = this.props;

        let cls = " ";

        if (isFunction(cellClassName)) {
            cls = cellClassName({ record, rowIndex, attrcode });
        } else {
            cls = cellClassName;
        }

        return ' ' + (cls || '') + ' ';
    }

    /**
     * @description: UI
     * @param {type}
     * @return:
     */
    getCellUI = () => {
        let {
            // beeTable render
            record,
            rowIndex,
            // 
            rowKey,
            // 
            attrcode,
            itemtype,
            hyperlinkflag,
            onCellHyperlinkTo,
            renderStatus = "edit",
            renderItem,
            // page
            renderItems = {},
            // 
            whichKeyToDisplay,
            // 
            UIProps,
            setCellProps,
            onTableCellClick,
            onBeforeEvent,
            onAfterEvent,
            onCellStatusChange,
            setCellValue,
            getCellValue,
            render,
            store,
            scale,
            //   item 
            loginLanguage,
            // =====================
            // 
            __col,
            onEnter,
            hotKeyWithArrow,
            // className 
            className,
            maxlength,
            //  ... this.props rowKey
            ...others
        } = this.props;
        // 
        let {
            disabled,
            // 
            tableEdit,
            editType,
            editable,
            disableModify,
            validated,
            autoFocus,
            isSagaStatus,
            renderItemInEdit,
            isSwitchType,
        } = this.cellStatus;
        // 
        let rowKeyValue = this.rowKeyValue;
        let cellScale = this.cellScale;
        let cellValue = this.cellValue;

        // 
        renderItem = renderItems[attrcode];
        // console.log(renderItem, renderItems);

        // 
        if (render && itemtype === "customer") {
            //   customer
            let customRender = render(
                cellValue,
                record,
                rowIndex,
                store,
            );
            if (!customRender) {
                return <div />;
            }
            if (!isValidElement(customRender)) { // react
                if (isObject(customRender)) {
                    console.error('invalid react element', customRender);
                    customRender = JSON.stringify(customRender);
                }
                return <div className="add-customer-cell un-react-element">{customRender}</div>;
            }

            //  TODO
            let content = {
                ...customRender,
                props: {
                    ...customRender.props,
                    renderInTable: true,
                },
            };

            return <div {...__col}>{cloneElement(content)}</div>;
        }

        // 
        if (!render && hyperlinkflag && !editable && !tableEdit) {
            //  TODO style 
            return (
                <span style={{ overflow: "hidden", display: 'flex', width: '100%' }}>
                    <a
                        href="javascript:;"
                        className='cell-hyper'
                        onClick={e => {
                            e.stopPropagation();
                            //
                            isFunction(onCellHyperlinkTo) && onCellHyperlinkTo(record, attrcode, cellValue);
                        }}
                    >
                        {cellValue.display || cellValue.value || ""}
                    </a>
                </span>
            );
        }
        // let ppp = {};
        // Array.from(new Array(50)).map((item, i) => { ppp['a' + i] = "a" + i })

        // console.time('jiegou1')
        // let a = <div {...ppp}> </div>;
        // console.timeEnd('jiegou1')
        let autoOpen = this.state.autoOpen ? {
            open: this.state.autoOpen,
            autoShow: this.state.autoOpen,
        } : {};
        return (
            <Item
                {...others}
                // {...this.props}
                // open 
                {...autoOpen}
                autoFocus={autoFocus}
                isCheckChinese={!!maxlength}
                maxlength={maxlength}
                renderInTable
                disabled={disabled}
                scale={cellScale}
                browse={!editable || isSagaStatus}
                whichKeyToDisplay={whichKeyToDisplay}
                itemtype={itemtype}
                attrcode={attrcode}
                // tabIndex={!disabled && editable && !isSagaStatus ? 0 : -1}
                editType={editType}
                disableModify={disableModify}
                onEnter={onEnter}
                hotKeyWithArrow={hotKeyWithArrow}
                setFieldValue={({ attrcode, value }) => {
                    // onChange
                    return (
                        isFunction(setCellValue) &&
                        setCellValue({
                            record,
                            rowKey,
                            rowKeyValue,
                            rowIndex,
                            attrcode,
                            value,
                            column: { ...__col, ...this.cellStatus },
                            triggerType: this.defaultItemCreator["valueChangePropName"] || "onChange",
                        })
                    );
                }}
                getFieldValue={({ attrcode }) => {
                    return this.getValue(this.props, attrcode, this.cellScale, editable);
                }}
                onBeforeEvent={async ({ value, event }) => {
                    store && store.setCellProps(rowKeyValue, attrcode, { validated: true }, false);
                    //  
                    // 
                    validated === false && store && store.setCellProps(rowKeyValue, attrcode, { validated: true }, false);

                    if (isFunction(onBeforeEvent)) {
                        let isContinue = (
                            (await onBeforeEvent({
                                record,
                                rowKey,
                                rowKeyValue,
                                rowIndex,
                                attrcode,
                                value,
                                event,
                                column: { ...__col, ...this.cellStatus },
                                triggerType: this.defaultItemCreator["beforeEventTrigger"] || "onFocus",
                                validated,
                            })) !== false
                        )
                        //  false  
                        // isContinue === false && this.setState({ showIcon: isContinue });
                        return isContinue;
                    }
                    return true;
                }}
                onAfterEvent={({ value, oldValue, componentValue, event }) => {
                    this.setState({ autoOpen: false });
                    //  
                    isFunction(onAfterEvent) &&
                        onAfterEvent({
                            record,
                            rowKey,
                            rowKeyValue,
                            rowIndex,
                            attrcode,
                            value,
                            componentValue,
                            event,
                            column: { ...__col, ...this.cellStatus },
                            oldValue,
                            triggerType:
                                this.defaultItemCreator["afterEventTrigger"] ||
                                "onBlur",
                        });
                }}
                onStatusChange={({ isEdit }) => {
                    store && store.setCellProps(rowKeyValue, attrcode, { focusToEdit: isEdit }, true);
                    this.setState({ autoOpen: !isEdit ? false : this.state.autoOpen, showIcon: !isEdit });
                    if (isFunction(onCellStatusChange)) {
                        onCellStatusChange({ isEdit, column: { ...__col, ...this.cellStatus }, rowIndex, attrcode })
                    }
                }}
                path={[rowKeyValue, attrcode]}
                // 
                renderItem={({ isEdit }) => {
                    if (render && renderStatus === "customer") {
                        let cellEdit = (isEdit || isSwitchType);
                        return render(
                            cellValue,
                            record,
                            rowIndex,
                            store,
                            renderItemInEdit ? (cellEdit ? "edit" : "editBrowse") : "browse",
                        );
                    }

                    // 
                    if (renderItemInEdit) {
                        // 
                        let cellEdit = (isEdit || isSwitchType);
                        if (cellEdit && render && renderStatus === "edit") {
                            return render(
                                cellValue,
                                record,
                                rowIndex,
                                store,
                            );
                        }

                        if (!cellEdit && render && renderStatus === "editBrowse") {
                            return render(
                                cellValue,
                                record,
                                rowIndex,
                                store,
                            );
                        }

                        // 
                        if (cellEdit && renderItem) {
                            return isFunction(renderItem)
                                ? renderItem.call(
                                    null,
                                    cellValue,
                                    record,
                                    rowIndex,
                                    store,
                                )
                                : renderItem;
                        }
                    } else {
                        // 
                        // 
                        //  labelcustomer
                        if (render && renderStatus === "browse") {
                            let customRender = render(
                                cellValue,
                                record,
                                rowIndex,
                                store,
                            );
                            if (!isValidElement(customRender)) {
                                if (isObject(customRender)) {
                                    console.error('invalid react element', customRender);
                                    customRender = JSON.stringify(customRender);
                                }
                                return <div className="add-customer-cell un-react-element">{customRender}</div>;
                            }
                            return customRender;
                        }
                    }
                    // console.log(isEdit, renderItem, render, renderStatus)
                }}
            />
        );
    }

    handleMouseEnter = () => {
        if (this.cellDom) {
            let { showTip } = this.state;
            let tipNode = null;
            tipNode = this.cellDom.getElementsByClassName('template-item-wrapper-inner')[0]
            if (tipNode) {
                if ((tipNode.scrollWidth > tipNode.clientWidth || tipNode.offsetHeight > 30) && showTip === false) {
                    let tooltipDelay
                    tipNode.scrollWidth > 4000 ? tooltipDelay = false : tooltipDelay = true
                    this.setState({ showTip: true, tooltipDelay });
                }
            }
        }
    }

    // 
    onCellClick = (tag, e) => {
        if (this.cellDom) {
            let $focusElement = this.cellDom.querySelector('.template-item-wrapper.template-item-browse[tabindex="0"]');
            if (tag == 'picker' || tag == 'select' || tag == 'refer') {
                this.setState({ autoOpen: true });
            }

            if ($focusElement) {
                //  
                $focusElement.ncExecuteFocus = true;
                // console.log($focusElement, 'xxx');
                this.cellDom.contains(e.target) && $focusElement.focus();
                // TODO 
                $focusElement.ncExecuteFocus = false;
            }
        }
    }

    render() {
        let cellUI = this.getCellUI();

        if (!cellUI) {
            return <div />;
        }

        let {
            // beeTable render
            record,
            rowIndex,
            // 
            bodyDisplayInRow = true,
            // 
            attrcode,
            itemtype,
            onTableCellClick,
            onCellKeyDown,
            onCellFocus,
            onCellMouseDown,
            onEnter,
            hotKeyWithArrow,
            tableId,
            __col,
            langJson = {},
        } = this.props;

        // 
        let {
            disabled, tableEdit, editable, selected, focused, cellIcon, actived, showDrag, validated, focusToEdit,
            leftLine,
            rightLine,
            bottomLine,
            topLine,
            renderType,
            isCheckboxSwitchType,
            isSagaStatus,
            isCellIconInRight,
        } = this.cellStatus;
        // className
        let hasCellIcon = tableEdit && this.state.showIcon && cellIcon !== 'none';
        let hasValue = !isWrong(this.cellValue.value ?? this.cellValue.display);

        let cellStyle = (tableEdit ? ' table-status-edit' : '') +
            (actived ? ' table-cell-actived' : '') +
            (focusToEdit ? ' table-cell-focus-blue' : '') +
            (focused ? ' table-cell-focused' : '') +
            (disabled ? ' table-cell-disabled' : '') +
            (editable ? ' table-cell-edit' : '') +
            (validated === false ? ' table-cell-valid-failed' : '') + '';

        let cellBgBdClassName = " table-cell-wrapper-bg " +
            (topLine ? ' table-cell-line-top' : '') +
            (leftLine ? ' table-cell-line-left' : '') +
            (rightLine ? ' table-cell-line-right' : '') +
            (bottomLine ? ' table-cell-line-bottom' : '') +
            cellStyle;

        let cellClassName = ' table-cell-wrapper ' +
            this.getCellClassName() +
            ' cell-icon-' + cellIcon +
            (selected ? ' table-cell-selected' : '') +
            cellStyle +
            (isCheckboxSwitchType ? ' table-cell-checkbox-switch' : '') +
            (hasCellIcon ? ' table-cell-show-icon' : '');
        // (hasCellIcon && hasValue && !isCellIconInRight ? ' table-cell-icon-left ' : ' ');
        // 
        let cellSelectorKey = this.rowKeyValue + '@' + attrcode;
        //  TODO 
        let hotKeyPrefix = CONFIG.hotKeyModuleIdPrefix + '-' + tableId + '-' + attrcode + '-' + rowIndex;
        let autoFocusIdentifier = {};
        autoFocusIdentifier[hotKeyPrefix] = hotKeyPrefix;

        // cellContentClassName
        let cellContentClassName = " cell-content " +
            (bodyDisplayInRow ? ' cell-ellipsis' : '') +
            (editable ? ' cell-edit' : '') +
            (!bodyDisplayInRow ? ' cell-fold-display' : '') +
            (itemtype === 'number' ? ' number-right ' : ' ');

        return <Fragment>
            <div className={cellBgBdClassName} cellSelectorKey={cellSelectorKey} data-code={attrcode} {...autoFocusIdentifier}>
                <div className="left-line line-line-line"></div>
                <div className="right-line line-line-line"></div>
                <div className="top-line line-line-line"></div>
                <div className="bottom-line line-line-line"></div>
            </div>
            <div
                fieldid={getSysFieldid(attrcode)}
                {...autoFocusIdentifier}
                className={cellClassName}
                ref={dom => {
                    this.cellDom = dom;
                }}
                onMouseEnter={this.handleMouseEnter}
                onClick={e => {
                    if (isSagaStatus) {
                        return;
                    }
                    // 
                    this.onCellClick('wrapper', e);

                    if (onTableCellClick) {
                        onTableCellClick(record, rowIndex, attrcode, __col, e);
                    }
                    // setCellStatus && setCellStatus({ rowKey, rowKeyValue, rowIndex, attrcode });
                }}
                // 
                cellSelectorKey={cellSelectorKey}
                data-type={itemtype}
                data-code={attrcode}
                data-renderType={renderType}
                onKeyDown={e => {
                    // console.log('cell onKeyDow trigger', e);
                    if (!focusToEdit && e.key === 'Enter') {
                        e.stopPropagation();
                        onEnter({ e, target: e.target, itemType: itemtype });
                    }

                    if (!focusToEdit && isArrowKeyDown(e)) {
                        e.stopPropagation();
                        hotKeyWithArrow && hotKeyWithArrow({ e, target: e.target, itemType: itemtype });
                    }

                    return onCellKeyDown && onCellKeyDown(record, rowIndex, attrcode, __col, e);
                }}

                // 
                onMouseDown={e => {
                    return onCellMouseDown && onCellMouseDown(record, rowIndex, attrcode, __col, e);
                }}
                //  !isEdit
                // tabIndex={!disabled && editable ? '0' : '-1'}
                onFocus={e => {
                    // console.log('onfocus trigger');
                    // 
                    this.cellDom && (this.cellDom.className += ' table-cell-focused');
                    return onCellFocus && onCellFocus(record, rowIndex, attrcode, __col, e);
                }}
                onBlur={e => {
                    // console.log('onblur trigger', e);
                    this.cellDom && (this.cellDom.className = this.cellDom.className.replace('table-cell-focused', ''));
                }}
            >
                {editable || !this.state.showTip ? <div
                    className={cellContentClassName}
                >
                    {cellUI}
                </div> :
                    <Tooltip
                        placement="top"
                        overlay={handleBrowseData(itemtype, this.cellValue.display, this.cellValue.value, this.cellScale, attrcode, __col.languageMeta, record.values || record, __col.datatype, __col.options, __col.multiple, langJson)}
                        className="tooltip-word-color high-table-tooltip"
                        delay={this.state.tooltipDelay ? 1 : undefined}
                        inverse>
                        <div
                            className={cellContentClassName}
                        >
                            {cellUI}
                        </div>
                    </Tooltip>
                }
                {editable && !isSagaStatus && this.state.showIcon && <CellIcon itemtype={itemtype} attrcode={attrcode} value={this.cellValue.value ?? this.cellValue.display} disabled={disabled} cellIcon={cellIcon} onClick={this.onCellClick} />}
            </div >
            {!disabled && showDrag && !isSagaStatus && <div cellDrag="on" cellSelectorKey={cellSelectorKey} className="table-cell-drag-icon iconfont"></div>}
        </Fragment>;
    }
}

export default Cell;