import React, { Component, Fragment } from 'react';

import { isFunction, filterColumns, isWrong, isObject } from '../../utils/index';

import { ItemProvider } from '@platform/template';
import { getSysFieldid, getLangCode } from "@platform/api";

import Title from './Title';
import Cell from './Cell';

import CONFIG from '../../config';

/**
 * @name: 
 * @test: test font
 * @msg: 
 * @param {type} 
 * @return: 
 */
export function createColumns(columns, others) {
    // TODO columns   Store

    // 
    columns = filterColumns(columns, item => {
        !item && console.warn('columns include empty element, please check columns data')
        return item.visible;
    });

    // 
    let loginLanguage = getLangCode();

    //  
    return columns.map(item => {
        if (item.children) {
            return {
                required: false,
                className: (item.className || '') + ' parent-column',
                key: item.attrcode,
                dataIndex: item.dataIndex || item.attrcode,
                title: <Title {...item} title={item.title || item.label} {...others} attrcode={item.attrcode || item.key} isParent />,
                children: createColumns(item.children, others),
            };
        }
        if (isWrong(item.width)) {
            if (item.itemtype === 'datetimepicker') {
                item.width = '240px';
            } else if (item.itemtype === 'rangepicker') {
                item.width = '390px';
            } else if (CONFIG.timeTypes.includes(item.itemtype)) {
                item.width = '160px';
            } else {
                item.width = '120px';
            }
        }

        return {
            ...item,
            required: false,
            // className
            className: (item.className ? item.className : ' ') + (item.unitActive ? ' column-header-active ' : ' ') + (item.itemtype === 'number' ? ' column-type-number ' : ' '),
            key: item.key || item.attrcode,
            redstar: item.required, //
            dataIndex: item.dataIndex || item.attrcode,
            title: <Title {...item} title={item.title || item.label} {...others} attrcode={item.attrcode || item.key} />,
            render: (text, record, index) => {
                let __col = item;
                item.key = item.key || item.attrcode;
                // return <div />;
                return {
                    children: <Cell
                        {...item}
                        {...others}
                        loginLanguage={loginLanguage}
                        cellIcon={others.cellIcon || item.cellIcon}
                        //  renderStatus  
                        renderStatus={others.renderStatus || item.renderStatus}
                        record={record}
                        text={text}
                        attrcode={item.attrcode || item.key}
                        rowIndex={index}
                        __col={__col} />,
                    props: isFunction(others.mergeCell) ? others.mergeCell({ record, index, attrcode: item.attrcode }) : {}, // 
                };
            },
        };
    });
}

/**
 * 
 * @param {*} Table 
 */
export default function formatColumn(Table) {
    if (!Table) {
        console.error('Table');
    }
    // TODO
    Table = ItemProvider(Table);
    return class MetaTable extends Component {
        constructor(props) {
            super(props);
        }

        render() {
            let {
                columns,
                langJson,
                getCellProps, setCellProps, onTableCellClick,
                onBeforeEvent, onAfterEvent,
                setCellValue, getCellValue,
                onCellStatusChange,
                rowKey,
                // name  tableId 
                name,
                tableId,
                status,
                bodyDisplayInRow,
                cellClassName,
                mergeCell,
                store,
                onCellHyperlinkTo,
                cellIcon,
                renderItems,
                renderStatus,
                onCellKeyDown,
                onCellMouseDown,
                onCellFocus,
                disableEditType,
                fieldid,
                hotKeyWithArrow,
                onEnter,
                ...others
            } = this.props;
            return <div className="use-column-component" fieldid={getSysFieldid(`${fieldid || name}_table`)}>
                <Table
                    rowKey={record => {
                        return isObject(record[rowKey]) ? record[rowKey].value : record[rowKey]
                    }}
                    bodyDisplayInRow={bodyDisplayInRow}
                    columns={createColumns(columns, {
                        getCellProps,
                        setCellProps,
                        onTableCellClick,
                        onBeforeEvent,
                        onAfterEvent,
                        onCellStatusChange,
                        setCellValue,
                        getCellValue,
                        rowKey,
                        status,
                        bodyDisplayInRow,
                        cellClassName,
                        mergeCell,
                        store,
                        onCellHyperlinkTo,
                        cellIcon,
                        renderItems,
                        renderStatus,
                        onCellKeyDown,
                        onCellMouseDown,
                        onCellFocus,
                        tableId,
                        langJson,
                        disableEditType,
                        hotKeyWithArrow,
                        onEnter,
                    })}
                    store={store}
                    tableId={tableId}
                    name={name}
                    langJson={langJson}
                    hotKeyWithArrow={hotKeyWithArrow}
                    onEnter={onEnter}
                    // 
                    // getCellClassName={(record, index, column) => {
                    //     let { attrcode } = column;
                    //     // console.log(record, index, column);
                    //     return `-td-${attrcode}-${index}`;
                    // }}
                    {...others}
                />
            </div>
        }
    }
}
